import {
  Component,
  OnInit,
  ViewChild,
  ElementRef,
  Renderer2,
  AfterViewInit,
} from '@angular/core';

interface Bilder {
  img: string;
  titel: string;
  nummer: number;
}

@Component({
  selector: 'app-bslider',
  templateUrl: './bslider.component.html',
  styleUrls: ['./bslider.component.scss'],
})
export class BsliderComponent implements OnInit, AfterViewInit {
  @ViewChild('slides') slides: ElementRef;

  // Bilder-Galerie zum Eshop
  bilder: Bilder[] = [
    { img: 'assets/blog/b1.png', titel: 'Registrieren', nummer: 0 },
    { img: 'assets/blog/b2.png', titel: 'Login', nummer: 1 },
    { img: 'assets/blog/b3.png', titel: 'Storyboard', nummer: 2 },
    { img: 'assets/blog/b4.png', titel: 'Post erstellen', nummer: 3 },
    { img: 'assets/blog/b5.png', titel: 'Post ansehen', nummer: 4 },
  ];

  // Slider-Einstellungen
  slideIndex = 1;

  plusSlides(n) {
    this.showSlides((this.slideIndex += n));
  }

  currentSlide(n) {
    this.showSlides((this.slideIndex = n));
  }

  showSlides(n) {
    let i;
    const slides = document.getElementsByClassName(
      'mySlides'
    ) as HTMLCollectionOf<HTMLElement>;
    const captionText = document.getElementById('caption') as HTMLImageElement;

    if (n > slides.length) {
      this.slideIndex = 1;
    }
    if (n < 1) {
      this.slideIndex = slides.length;
    }
    for (i = 0; i < slides.length; i++) {
      slides[i].style.display = 'none';
    }
    slides[this.slideIndex - 1].style.display = 'block';
    captionText.innerHTML = this.bilder[this.slideIndex - 1].titel;
  }

  //  onInit
  ngOnInit(): void {}

  ngAfterViewInit() {
    this.showSlides(this.slideIndex);
  }
}
