import { PsliderComponent } from './Components/projekte/design/photoshop/pslider/pslider.component';
import { AftereffectComponent } from './Components/projekte/design/aftereffect/aftereffect.component';
import { PhotoshopComponent } from './Components/projekte/design/photoshop/photoshop.component';
import { DesignComponent } from './Components/projekte/design/design.component';
import { BsliderComponent } from './Components/projekte/webentwicklung/blog/bslider/bslider.component';
import { VsliderComponent } from './Components/projekte/webentwicklung/verein/vslider/vslider.component';
import { SliderComponent } from './Components/projekte/eshop/slider/slider.component';
import { MainPageComponent } from './Components/main-page/main-page.component';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { APP_INITIALIZER } from '@angular/core';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { NavigationComponent } from './Components/main-page/navigation/navigation.component';
import { ModeToggleService } from 'src/services/modeToggle.service';
import { StorageService } from 'src/services/local-storage.service';
import { AboutMeComponent } from './Components/about-me/about-me.component';
import { KontaktComponent } from './Components/kontakt/kontakt.component';
import { ProjektGalerieComponent } from './Components/projekt-galerie/projekt-galerie.component';
import { WebentwicklungComponent } from './Components/projekte/webentwicklung/webentwicklung.component';
import { VereinComponent } from './Components/projekte/webentwicklung/verein/verein.component';
import { BlogComponent } from './Components/projekte/webentwicklung/blog/blog.component';
import { SkillsComponent } from './Components/skills/skills.component';
import { ThumbnailComponent } from './Components/thumbnail/thumbnail.component';
import { EshopComponent } from './Components/projekte/eshop/eshop.component';

export function themeFactory(themeService: ModeToggleService) {
  return () => themeService.setThemeOnStart();
}

@NgModule({
  declarations: [
    AppComponent,
    NavigationComponent,
    MainPageComponent,
    AboutMeComponent,
    KontaktComponent,
    ProjektGalerieComponent,
    WebentwicklungComponent,
    VereinComponent,
    BlogComponent,
    SkillsComponent,
    ThumbnailComponent,
    EshopComponent,
    SliderComponent,
    VsliderComponent,
    BsliderComponent,
    DesignComponent,
    PhotoshopComponent,
    AftereffectComponent,
    PsliderComponent,
  ],
  imports: [BrowserModule, AppRoutingModule],
  providers: [
    ModeToggleService,
    StorageService,
    {
      provide: APP_INITIALIZER,
      useFactory: themeFactory,
      deps: [ModeToggleService],
      multi: true,
    },
  ],
  bootstrap: [AppComponent],
})
export class AppModule {}
