import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class NavService {
  // Verantwortlich dafür, dass die main-page mit der Navigation kommunizieren kann und Methoden austauscht

  private subject1 = new Subject<any>();
  private subject2 = new Subject<any>();
  private subject3 = new Subject<any>();
  private subject4 = new Subject<any>();
  private subject5 = new Subject<any>();

  sendClickEvent1() {
    this.subject1.next();
  }
  sendClickEvent2() {
    this.subject2.next();
  }
  sendClickEvent3() {
    this.subject3.next();
  }
  sendClickEvent4() {
    this.subject4.next();
  }
  sendClickEvent5() {
    this.subject5.next();
  }

  getClickEvent1(): Observable<any> {
    return this.subject1.asObservable();
  }
  getClickEvent2(): Observable<any> {
    return this.subject2.asObservable();
  }

  getClickEvent3(): Observable<any> {
    return this.subject3.asObservable();
  }

  getClickEvent4(): Observable<any> {
    return this.subject4.asObservable();
  }

  getClickEvent5(): Observable<any> {
    return this.subject5.asObservable();
  }
}
