import { DesignComponent } from './Components/projekte/design/design.component';
import { MainPageComponent } from './Components/main-page/main-page.component';

import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { WebentwicklungComponent } from './Components/projekte/webentwicklung/webentwicklung.component';
import { EshopComponent } from './Components/projekte/eshop/eshop.component';

const routes: Routes = [
  { path: '', redirectTo: '/Startseite', pathMatch: 'full' },
  { path: 'Startseite', component: MainPageComponent },
  { path: 'Webentwicklung', component: WebentwicklungComponent },
  { path: 'Eshop', component: EshopComponent },
  { path: 'Design', component: DesignComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule {}
